/* Image Carousel Component Styles */

.container {
  display: flex;
  flex-direction: column;
  gap: var(--space-4);
}

.container:focus {
  outline: none;
}

.empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: var(--space-4);
  height: 300px;
  background: var(--color-surface);
  border: var(--border-width) dashed var(--color-border);
  border-radius: var(--radius-lg);
  color: var(--color-text-muted);
}

.empty p {
  font-size: var(--text-sm);
  margin: 0;
}

/* Main image */
.mainImage {
  position: relative;
  background: var(--color-bg-tertiary);
  border-radius: var(--radius-lg);
  overflow: hidden;
}

.mainImage img {
  width: 100%;
  height: auto;
  max-height: 500px;
  object-fit: contain;
  cursor: zoom-in;
}

/* Navigation buttons */
.navButton {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(10, 22, 40, 0.8);
  border: var(--border-width) solid var(--color-border);
  border-radius: var(--radius-full);
  color: var(--color-text-primary);
  opacity: 0;
  transition: all var(--transition-base);
}

.container:hover .navButton,
.container:focus-within .navButton {
  opacity: 1;
}

.navButton:hover {
  background: var(--color-surface);
  color: var(--color-accent-primary);
}

.prevButton {
  left: var(--space-4);
}

.nextButton {
  right: var(--space-4);
}

/* Image info overlay */
.imageInfo {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--space-4);
  background: linear-gradient(transparent, rgba(10, 22, 40, 0.9));
}

.passesLabel {
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  color: var(--color-text-primary);
  background: var(--color-accent-subtle);
  padding: var(--space-1) var(--space-3);
  border-radius: var(--radius-full);
  border: var(--border-width) solid var(--color-accent-primary);
}

.counter {
  font-size: var(--text-sm);
  font-family: var(--font-mono);
  color: var(--color-text-secondary);
}

/* Thumbnails */
.thumbnails {
  display: flex;
  gap: var(--space-2);
  overflow-x: auto;
  padding: var(--space-2);
  background: var(--color-surface);
  border-radius: var(--radius-lg);
}

.thumbnail {
  position: relative;
  flex-shrink: 0;
  width: 80px;
  height: 60px;
  border: var(--border-width-2) solid transparent;
  border-radius: var(--radius-md);
  overflow: hidden;
  transition: all var(--transition-base);
}

.thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.thumbnail:hover {
  border-color: var(--color-border-light);
}

.thumbnail.active {
  border-color: var(--color-accent-primary);
}

.thumbnailLabel {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  font-size: 10px;
  font-weight: var(--font-medium);
  text-align: center;
  padding: 2px;
  background: rgba(10, 22, 40, 0.8);
  color: var(--color-text-secondary);
}

.thumbnail.active .thumbnailLabel {
  background: var(--color-accent-primary);
  color: var(--color-text-inverse);
}



