/* Spinner Component Styles */

.spinner {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: var(--color-accent-primary);
}

.spinner svg {
  animation: spin 1s linear infinite;
}

.track {
  opacity: 0.2;
}

.progress {
  opacity: 1;
}

/* Sizes */
.sm svg {
  width: 16px;
  height: 16px;
}

.md svg {
  width: 24px;
  height: 24px;
}

.lg svg {
  width: 32px;
  height: 32px;
}

.xl svg {
  width: 48px;
  height: 48px;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



